import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Execucao_formulario } from '../../../modelo/Execucao_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'execucao_formulario-dados-details',   templateUrl: 'execucao_formulario_dadosDetails.component.html' }) export class Execucao_formularioDadosDetailsComponent implements OnInit {    execucao_formulario: Execucao_formulario;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.execucao_formulario.Execucao_formulario";                 let id = +params['id'];         this.execucao_formulario = new Execucao_formulario();         this.execucao_formulario.cd_execucao_formulario = id;         o.objetoJson = this.execucao_formulario;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a execucao_formulario. '; }      this.execucao_formulario = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


